@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
* {
  box-sizing: border-box;
  --orange: #d87d07;
  --white: white;
  --dark-gray: #14171d;
  --dark-gray-transparent: #14171da8;
  --green: #00e300;
}

body {
  margin: 0;
  font-family: 'Roboto';
  background-color: var(--dark-gray);
  color: var(--orange);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  flex-direction: column;
  
  width: 100vw;
  padding: 5%;
}
.Button-panel {
  position: fixed;
  bottom: 10px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 10px;
}

.Create-todo {
  width: 60px;
  height: 60px;
  border: none;
  font-size: 3rem;
  font-weight: bolder;
  border-radius: 100%;
  color: white;
  background-color: var(--orange);
  transition: -webkit-filter .1s;
  transition: filter .1s;
  transition: filter .1s, -webkit-filter .1s;
}

.Create-todo:hover {
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}

.Delete-todo {
  height: 30px;
  padding: 0 15px;
  border: none;
  font-size: .8rem;
  font-weight: bolder;
  border-radius: 100px;
  color: white;
  background-color: var(--orange);
  transition: -webkit-filter .1s;
  transition: filter .1s;
  transition: filter .1s, -webkit-filter .1s;
}

.Delete-todo:hover {
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}


.Counter {
  text-align: center;
}
.Todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid #d87d07;
  margin-bottom: 10px;
}
.Todo-item:before {
  position: absolute;
  display: block;
  content: "";
  width: 90%;
  height: 2px;
  margin-top: 52px;
  box-shadow: 0 0 10px rgba(0,0,0,1);
}

.Todo-item div {
  display: flex;
  align-items: center;
}

.Todo-item__text {
  padding: 0px 10px;
}

.Icon-container {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px;
}

.Icon-container__check {
  border: 2px solid var(--green);
  border-radius: 100%;
  padding: 3px;
}

.Icon-container__delete {
  width: 30px;
  height: 30px;
}

.Icon-svg__delete {
  width: 40px;
  height: 40px;
}


.Icon-container:hover {
  -webkit-filter:brightness(70%);
          filter:brightness(70%)
}
.Todo-list {
  list-style: none;
  min-width: 300px;
  padding: 0;
}
.Searcher {
  border: none;
  padding:10px 15px;
  border-radius: 100px;
  font-size: 1rem;
  background-color: var(--orange);
  color: white;
  margin: 20px 0;
}

.Searcher::-webkit-input-placeholder {
  color: white;
}

.Searcher:-ms-input-placeholder {
  color: white;
}

.Searcher::placeholder {
  color: white;
}
.Create-Todo__Container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--dark-gray-transparent);
  
  z-index: 10;
}

.Create-Todo__Form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  height: 220px;
  background-color: var(--dark-gray);
  padding: 10px;
  border-radius: 10px;
  box-shadow:0px 0px 10px rgba(0,0,0,0.5);
  color: var(--white);
}

.Form__title {
  text-align: center;
}

.Form__description-input {
  border: 2px solid white;
  padding:10px 15px;
  border-radius: 100px;
  font-size: 1rem;
  background-color: var(--dark-gray);
  color: var(--white);
  margin: 20px 0;
  width: 90%;
}

.Form__description::-webkit-input-placeholder {
  color: var(--white);
}

.Form__description:-ms-input-placeholder {
  color: var(--white);
}

.Form__description::placeholder {
  color: var(--white);
}

.Form__submit-button {
  padding: 7px 15px;
  border: none;
  border-radius: 100px;
  font-size: 1rem;
  margin: 20px;
}

.Close-container {
  position: absolute;
  top: 10px;
  right: 10px;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

.Close-container::before {
  
  display: block;
  content: "";
  background-image: url(/simply-react-todo/static/media/close.bb8b6a19.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 60px;
  height: 60px;
  -webkit-filter:drop-shadow(0px 0px 2px rgba(0,0,0,0.5));
          filter:drop-shadow(0px 0px 2px rgba(0,0,0,0.5))
}

.Todo-skeleton {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 52px;
  opacity: 30%;
  margin-bottom: 10px;
}
.Todo-skeleton:before {
  position: absolute;
  display: block;
  content: "";
  width: 90%;
  height: 2px;
  margin-top: 52px;
  
}

.check-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  
}

.skeleton__check-todo {
  border-radius: 100%;
}
.skeleton__check-todo:before {
  display: block;
  content: "";
  background-color: var(--dark-gray);
  border-radius: 100%;
  background-repeat: no-repeat;
  background-size: 15px 15px;
  background-position: 40%;
  width: 20px;
  height: 20px;
  border-radius: 100%;
}


.skeleton__orange-filter {
  -webkit-filter: invert(48%) sepia(79%) saturate(2476%) brightness(118%) contrast(119%);
          filter: invert(48%) sepia(79%) saturate(2476%) brightness(118%) contrast(119%);
  transition: -webkit-filter .1s;
  transition: filter .1s;
  transition: filter .1s, -webkit-filter .1s;
}

.skeleton__cancel-todo:before {
  display: block;
  content: "";
  background-image: url(/simply-react-todo/static/media/close.bb8b6a19.svg);
  background-repeat: no-repeat;
  width: 30px;
  height: 30px;
}

.Todo-skeleton:before,
.check-container {
  background: linear-gradient(90deg, rgb(143, 142, 140) 35%, rgba(216,87,7,1) 100%);
  background-size: 400% 400%;
  -webkit-animation: LoadingAnimation 2s ease-in-out infinite;
          animation: LoadingAnimation 2s ease-in-out infinite;
}

@-webkit-keyframes LoadingAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes LoadingAnimation {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}
